import React, { Component } from 'react';
import './Blog.css';
import axios from 'axios';
import marked from 'marked'
import hljs from 'highlight.js'
import 'highlight.js/styles/github.css';
import { Link } from 'react-router-dom';



export default class Blog extends Component {
    constructor(props) {
        super(props);
       
        console.log(this.props.match.params);
        
        this.state = {
            title: this.props.match.params.path2,
            content: ''
        }

    }
    componentWillMount() {
        marked.setOptions({
            highlight: code => hljs.highlightAuto(code).value
        });
        var url1=' https://raw.githubusercontent.com/hugo1o1/posts/master/';
        var path = this.props.match.params.path1+'/'+this.props.match.params.path2;
        var name = path.slice(path.indexOf('/'));
        axios.get(url1 + path+name+'.md').then(res => {

            
            this.setState({
                
                content: marked(res.data)
            })
        })
    }

    componentDidMount() {
       
    }

    render() {
        return ( 
        <div className = 'blogContent' >
                <h1 className = 'title' > { this.state.title } </h1> 
                <div className = 'blog' dangerouslySetInnerHTML = {{ __html: this.state.content }}></div> 
                <Link to='/blogs' ><button className = 'viewmore' > view more </button></Link>
            </div>

    )
}
}